.horizontal-slider {
    width: 100%;
    max-width: 20em;
    height: 5vh;
    margin-left: 1em;
    align-items: center;
}

.example-thumb {
    cursor: pointer;
    position: absolute;
    z-index: 100;
    background: #ffffff;
    border: 5px solid #34495E;
    border-radius: 100%;
    display: block;
    box-shadow: 0 0 2px 0 rgb(0 0 0 / 44%);
    margin-bottom: 5px;
}

.example-thumb3 {
    /* DS */
    cursor: pointer;
    position: absolute;
    z-index: 100;
    background: #ffffff;
    border: 5px solid #34495e;
    border-radius: 100%;
    display: block;
    box-shadow: 0 0 2px 0 rgb(0 0 0 / 44%);
    margin-bottom: 5px;
}

.example-thumb.active {
    background-color: #ffffff;
}

.example-track1 {
    position: relative;
    background: #34495e;
}

.example-track2 {
    background: #34495e;
}

.example-track3 {
    background: #35978f;
}

.example-thumb1 {
    border: 5px solid #34495e;
}

.example-thumb2 {
    border: 5px solid #34495e;
}

.example-thumb3 {
    border: 5px solid #35978f;
}

.horizontal-slider .example-track1 {
    top: 20px;
    height: 4px;
}

.horizontal-slider .example-thumb1 {
    top: 12px;
    width: 10px;
    outline: none;
    height: 10px;
    line-height: 48px;
}

.horizontal-slider .example-thumb1::before {
    content: '''\00a0';
}

.horizontal-slider .example-track2 {
    top: 20px;
    height: 4px;
}

.horizontal-slider .example-thumb2 {
    top: 12px;
    width: 10px;
    outline: none;
    height: 10px;
    line-height: 48px;
}

.horizontal-slider .example-thumb2::before {
    content: none;
}

.horizontal-slider .example-thumb2::after {
    content: '\00a0''';
}

.horizontal-slider .example-track3 {
    top: 20px;
    height: 4px;
}

.horizontal-slider .example-thumb3 {
    top: 12px;
    width: 10px;
    outline: none;
    height: 10px;
    line-height: 48px;
}

.horizontal-slider .example-thumb3::before {
    content: '$''\00a0';
}

.horizontal-slider .example-thumb3::after {
    content: none;
}

.sliderWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: .5em;
}

.slideMargin {
    margin: 1em 0 0 1.5em;
}

.slideMargin3 {
    margin: 0.5em 0 0 1em;
}


/* Chart */

#chart {
    padding: 1ex 0 1ex 0;
    /* top | right | bottom | left */
    width: 90vw;
    height: 50vh;
    align-items: center;
}

.c3-line {
    stroke-width: 3px;
}

.c3-axis-y>.tick,
.c3-axis-y-label {
    fill: black;
}

.c3-axis-y2>.tick,
.c3-axis-y2-label {
    fill: #35978f;
}

.c3-axis-x-label {
    transform: translate(0px, 7px);
}

svg text {
    font-size: 15px;
}

